import store from '../store'

export const beforeHome = (to, _, next) => {
  next()
  // if(!store.getters.posts.length) {
    store.dispatch('showLoading')
    store.dispatch('getPosts')
      .then(() => {
        store.dispatch('hideLoading')
      })
  // }
}

export const beforePosts = (to, _, next) => {
  next()
  store.dispatch('showfetchingMore')
  store.dispatch('getPosts', to.params.limit)
    .then(() => {
      store.dispatch('hidefetchingMore')
    })
}

export const beforePost = (to, _, next) => {
  next()
  store.dispatch('showLoading')
  store.dispatch('getPost', to.params.postId)
    .then(() => {
      store.dispatch('hideLoading')
    })
}



